.lastComics-uppertitle {

  font-style: normal;
  font-weight: 500;
  font-size: 44px;

  align-items: center;
  letter-spacing: -0.02em;
  text-transform: uppercase;
  padding-bottom: 64px;
  color: var(--light-font-color);
}

@media screen and (max-width: 480px) {
  .lastComics-uppertitle {
    font-size: 28px;
    padding-bottom:44px;
  }
}

.lastComics-svg {
  fill: #34387F;
  cursor: pointer
}

@media screen and (max-width: 768px) {
  .lastComics-svg {
    display: none;
  }
}

.lastComics-header {
  display: flex;
  justify-content: space-between;
}

.lastComics-list {
  display: flex;
   /* align-items: center; */
   /* justify-content: center; */
  column-gap: 16px;
  margin-bottom: 127px;
} @media screen and (max-width: 768px) {
  .lastComics-list {  
    overflow-x: auto;
    margin-bottom: 0;
  }
}

.lastComics-list::-webkit-scrollbar {
  display: none;
}

.lastComics-item {
  width: 448px;
  cursor: pointer;
} @media screen and (max-width: 768px) {
  .lastComics-item {
      display: initial;
      flex-shrink: 0;
      flex-basis: auto;
      width: 448px;
  }
} @media screen and (max-width: 480px) {
  .lastComics-item {
    width: 323px;
  }
}


.lastComics-img {
  width: 448px;
  border-radius: 8px;
} @media screen and (max-width: 768px) {
  .lastComics-img {
    width: 448px;
  }
} @media screen and (max-width: 480px) {
  .lastComics-img {
    width: 323px;
  }
}

.lastComics-span {
  display: flex;
  flex-direction: column;
  padding-top: 16px;
} @media screen and (max-width: 480px) {
  .lastComics-span {
    padding-top: 14px;
  }
}

.lastComics-date {
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  display: flex;
  align-items: center;
  letter-spacing: -0.02em;
  padding-bottom: 8px;

  color: var(--light-font-color);
} @media screen and (max-width: 480px) {
  .lastComics-date {
    padding-bottom: 4px;
    font-size: 18px;
  }
}

.lastComics-name {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  align-items: center;

  color: rgba(250, 250, 250, 0.5);
} @media screen and (max-width: 480px) {
  .lastComics-name {
    font-size: 14px;
  }
}

.lastComics-container-btn {
  display: none;
}

@media screen and (max-width: 768px) {
  .lastComics-container-btn {
    display: flex;
    flex-direction: inherit;
    justify-content: center;
    align-items: center;
    gap:16px;
    padding-top: 32px;
    padding-bottom: 128px;
  }
} @media screen and (max-width: 480px) {
  .lastComics-container-btn {
    gap: 14px;
    padding-top: 20px;
    padding-bottom: 80px;
  }
}

@media screen and (max-width: 768px) {
  .lastComics-scroll-btn {
    display: grid;
    place-content: center;
    border: none;
    cursor: pointer;

    width: 44px;
    height: 44px;

    border: 1px solid #fafafa;
    border-radius: 50%;

    background: transparent url("../images/svg/Vector-11.svg") no-repeat center;
  }
  
  .lastComics-scroll-btn:focus{
      background: #34387F url("../images/svg/Vector-11-white.svg") no-repeat center;
      border-color: #34387F;
    }
    .lastComics-scroll-btn--right{
      transform: rotate(180deg);
    }
} @media screen and (max-width: 480px) {
  .lastComics-scroll-btn {
    background: transparent url("../images/svg/Vector-11.svg") no-repeat center/50%;
    width: 32px;
    height: 32px;
  }  .lastComics-scroll-btn:focus {
     background: #34387F url("../images/svg/Vector-11-white.svg") no-repeat center/50%;
     border-color: #34387F;
   }
  
   .lastComics-scroll-btn--right {
     transform: rotate(180deg);
   }
}

